import { v1 } from "uuid";

export type UserStateType = {
    users: Array<UserType>
}
export type UserType = {
    id: string
    followed: boolean
    photoUrl: string
    fullName: string
    status: string
    location: Array<LocationType>;
}
export type LocationType = {
    city: string
    country: string
}
export type ActionTypes =
    | ReturnType<typeof followAC>
    | ReturnType<typeof unfollowAC>
    | ReturnType<typeof setUsersAC>;

let initialState: UserStateType = {
    users: [
        {
            id: v1(),
            followed: false,
            photoUrl:"https://i.pinimg.com/550x/31/23/2f/31232fe4b51b47763282524f008d9081.jpg",
            fullName: 'Anana',
            status: 'Bathing - ',
            location: [{ city: 'Pinsk', country: 'Belarus' }]
        },
        {
            id: v1(),
            followed: true,
            photoUrl:"https://s.ws.pho.to/img/index/ai/source.jpg",
            fullName: 'Oil',
            status: '',
            location: [{ city: 'Grodno', country: 'Belarus' }]
        },
        {
            id: v1(),
            followed: false,
            photoUrl:"https://images.unsplash.com/photo-1626847152272-c64724db41c4?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwcm9maWxlLXBhZ2V8Mnx8fGVufDB8fHx8&w=1000&q=80",
            fullName: 'Main',
            status: '',
            location: [{ city: 'Reikiiavick', country: 'hz' }]
        },
    ]
}

export const UserReducer = (state: UserStateType = initialState, action: ActionTypes): UserStateType => {
    switch (action.type) {
        case "FOLLOW":
            return {
                ...state,
                users: state.users.map(u => u.id === action.userId ? { ...u, followed: true } : u)
            }
        case "UNFOLLOW":
            return {
                ...state,
                users: state.users.map(u => u.id === action.userId ? { ...u, followed: false } : u)
            }
        case "SET_USERS":
            return { ...state, users: [...state.users, ...action.users]}

        default:
            return { ...state }



    }
};
export const followAC = (userId: string) => {
    return {
        type: "FOLLOW",
        userId: userId
    } as const //   
}
export const unfollowAC = (userId: string) => {
    return {
        type: "UNFOLLOW",
        userId: userId
    } as const
}
export const setUsersAC = (users: Array<UserType>) => {
    return {
        type: "SET_USERS",
        users: users
    } as const
}